<template>
  <b-card>
    <div slot="header" v-if="cardTitle !== 'none'">
      {{allPosts[0].from}} till {{allPosts[0].to}}
    </div>
    <div style="display:table;width:100%;" v-for="(eachPost, postIndex) in allPosts" v-bind:key="postIndex">
      <div class="container" v-for="(everyDay, dayIndex) in eachPost.values" v-bind:key="dayIndex">
        <template v-if="eachPost.values[dayIndex+1]">
          <div class="row" v-bind:class="(dayIndex === 0) ? 'food-header-zero' : 'food-header'" v-if="everyDay['Date'] === eachPost.values[dayIndex+1].Date">
            <div class="col">
              <h3>{{everyDay['Date']}} </h3>
              <b-badge class="padding-left-10" variant="primary">{{everyDay['Day']}}</b-badge>
            </div>
          </div>
        </template>
        <div class="row food-row">
          <!-- <div class="col-sm-12 col-md-6 col-lg-4" v-for="(eachHeader, headerIndex) in allFields" v-bind:key="headerIndex" v-if="eachHeader !== 'Date' && eachHeader !== 'Day' && eachHeader !== 'Option'"> -->
          <div class="col-sm-12 col-md-4 col-lg-4" v-for="(eachHeader, headerIndex) in allFields" v-bind:key="headerIndex" v-if="eachHeader !== 'Date' && eachHeader !== 'Day'">
            <span v-if="eachHeader !== 'Option'">
              <code>{{eachHeader}}</code>
              <div>{{ everyDay[eachHeader] }}</div>
            </span>
            <div v-else>
              <strong>{{everyDay[eachHeader]}}</strong>
            </div>
          </div>
        </div>
        <!-- <div v-if="everyDay.Option === 'Dinner'">
          <h4><b-badge>Dinner</b-badge></h4>
          <b-col>
          <span v-for="(eachHeader, headerIndex) in allFields" v-bind:key="headerIndex">
            <span v-if="everyDay[eachHeader] !== 'Dinner'">
              <code>{{eachHeader}}</code>
              <span>{{ everyDay[eachHeader] }}</span>
            </span>
          </span>
          </b-col>
        </div> -->
      </div>
    </div>

    <!-- <b-row>
      <b-col sm="12" lg="12" v-for="(eachPost, postIndex) in allPosts" v-bind:key="postIndex">
        <b-card >
          <div class="h4 m-0">{{ eachPost.category }}</div>
          <b-table :hover="true" :striped="true" :bordered="true" :small="true" 
            :fixed="true" responsive="lg" :items="eachPost.values" :fields="allFields">
          </b-table>
        </b-card>
      </b-col>
    </b-row> -->
    <!-- <div v-if="card-title === ''" slot="footer"> Food Menu </div> -->
  </b-card>
</template>

<script>
let postsRef = firebase.database().ref('posts');

export default {
  name: 'foodMenu',
  props: {
    cardTitle: String
  },
  data() {
    return {
      allPosts: [],
      allFields: []
    }
  },
  created() {
    postsRef.on('child_added', snapshot => {
      this.allPosts.push(snapshot.val());
      this.allFields = Object.keys(this.allPosts[0].values[0]);
    });
  }
}
</script>

<style>
.food-row {
  margin-bottom: 20px;
}
.food-header {
  padding: 40px 0 30px 0;
  text-align: center;
  border-top: 1px dashed gray;
}
.food-header-zero {
  padding: 10px 0 30px 0;
  text-align: center;
}
</style>



